.banner{position:relative;height:40vh;padding:30px 0;overflow:hidden}.banner:hover .banner__arrow{opacity:1}@media(min-width: 768px){.banner{padding:60px 0;height:35vh}}.banner__background{position:absolute;display:flex;flex-direction:row;top:0;left:0;width:100%;height:100%;transition:transform 1s ease;filter:brightness(0.7)}.banner__slide{position:absolute;top:0;left:0;width:100vw;height:100%;background-size:cover;background-position:center;opacity:0;transition:opacity 1s ease;z-index:0}.banner__slide.active{opacity:1;z-index:1}.banner .slide-1{background-image:url("../../assets/slide-1-desktop.webp")}.banner .slide-2{background-image:url("../../assets/slide-2-desktop.webp")}.banner .slide-3{background-image:url("../../assets/slide-3-desktop.webp")}.banner__arrow{opacity:0;transition:opacity .3s,box-shadow .3s;position:absolute;top:50%;transform:translateY(-50%);width:70px;height:45px;z-index:2;background:rgba(0,0,0,0);border:none;cursor:pointer}.banner__arrow:hover .arrow-icon{opacity:1;filter:drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.8))}.banner .arrow-icon{width:62px;height:82px;transition:filter .3s ease;stroke-width:3}.banner .left{left:20px}.banner .right{right:20px}